










































import {Component, Mixins, Prop, Vue} from "vue-property-decorator";
import {formPageMixin} from "@/mixins/formPageMixin";
import {addObj, putObj} from "@/api/manage/sys/sysUserApi";
import { encryption } from '@/utils/password'

@Component({
  components: {}
})
export default class SysUserForm extends Mixins(formPageMixin) {
  @Prop() title!: string;
  @Prop({default: []}) public readonly orgTreeData!: [];

  reqFunctions = {
    create: addObj,
    update: putObj
  }

  createdFormCallback() {
    this.formObj={status:1};
  }

  echoDataProcess(record: any) {
    record.password = "";
  }

  redioChange(event: any) {
    console.log(event);
  }

  submitDataProcess (data:any) {
    data.password = encryption(data.password)
    return data
  }

  beforeStartSubmit() {
    let list = this.formObj.organizationId;
    this.formObj.organizationId = list[list.length - 1];
    return true;
  }

  changeOrg(list: any) {
    if (list) {
      this.formObj.organizationId = list[list.length - 1];
    }
  }

  formRules = {
    username: [
      {required: true, message: '', trigger: 'blur'}
    ]
  }

}
