import apiCore from "@/utils/apiCore";


const HTTP = new apiCore({
    baseURL: process.env.VUE_APP_URL,
    headers: {
        'Content-Type': 'application/json;charset=utf-8',
    },
});


export function getPage (params: object) {
    return HTTP.get('/sysuser/page', params);
}

export function getObj (id:number) {
    return HTTP.get('/sysuser/' + id, {});
}

export function addObj (data:object) {
    return HTTP.post('/sysuser', data);
}

export function putObj (data:object) {
    return HTTP.post('/sysuser/updateUserInfo', data);
}

export function delObj (id:any) {
    return HTTP.delete('/sysuser/' + id, {});
}


export function getUserScope(userId:number) {
    return HTTP.get('/sysuser/scope/'+ userId, {});
}


export function putUserScope(userId:number, userScope:any) {
    return HTTP.post('/sysuser/scope/'+ userId, userScope);
}


